.btnAdd { 
    border: 1px solid white;
    padding: 0.8em 2.5em;
    border-radius: 1em;
    background: none;
    color: white;
    font-size: 0.85em;
    font-weight: 550;
    transition: background-color .25s ease-in-out, color .25s ease-in-out;
}
.btnAdd:hover { 
    background-color: white;
    color: #121212;
    box-shadow: 0 4px 12px rgba(255, 255, 255, 0.2);

}
  
.modalOverlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    overflow: hidden; /* Запрещаем прокрутку фона */
  }
  
  /* Модальное окно */
  .modal {
    background-color: #121212;
    padding: 2em;
    border-radius: 1em;
    max-width: 500px;
    width: 90%;
    max-height: 90%; /* Устанавливаем максимальную высоту */
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.25);
    position: relative;
    animation: fadeIn 0.3s ease;
    overflow-y: auto; /* Включаем вертикальную прокрутку */
  }
  .closeBtn {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    color: #ffffff;
    font-size: 1.8rem;
    cursor: pointer;
    transition: color 0.2s ease;
  }
  
  .closeBtn:hover {
    color: #ff4d4d;
  }

.profile {
    padding: 20px;
    background-color: #f9f9f9;
  }
  
  .profile h2 {
    font-size: 2rem;
    margin-bottom: 20px;
  }
  
  .profileInfo {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .profileInfo p {
    font-size: 1.2rem;
  }
  
  .profileInfo strong {
    font-weight: bold;
  }
  
  .avatar {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    object-fit: cover;
    margin-top: 10px;
  }

  @media (max-width: 1200px) {
    .btnAdd { 
      font-size: 0.8em;
      font-weight: 500;
      padding: 0.7em 2.3em;
    }
  }
  @media (max-width: 768px) {
    .btnAdd { 
      font-size: 0.7em;
      font-weight: 500;
      padding: 0.7em 2.2em;
    }
  }
  @media (max-width: 550px) { 
    .btnAdd { 
      font-size: 0.65em;
      font-weight: 500;
      padding: 0.8em 1.5em;
    }
    .modal {
      margin: 0 0.5em;
    }
  }