.moderation {
    padding: 20px;
  }
  
  .adItem {
    border: 1px solid #ccc;
    padding: 15px;
    margin-bottom: 15px;
    border-radius: 5px;
  }
  
  .actions {
    margin-top: 10px;
  }
  
  .approve {
    background-color: #4caf50;
    color: white;
    border: none;
    padding: 10px 15px;
    margin-right: 10px;
    cursor: pointer;
    border-radius: 3px;
  }
  
  .reject {
    background-color: #f44336;
    color: white;
    border: none;
    padding: 10px 15px;
    cursor: pointer;
    border-radius: 3px;
  }
  
  .loading,
  .noAds {
    text-align: center;
    font-size: 18px;
    color: #555;
  }
  