/* General Styles for Card */
.card { 
  background-color: #121212;
  padding: 1.5em;
  border-radius: 1.5em;
  width: 45%;
  max-height: 65vh;
  margin-bottom: 1em;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.cardUser{ 
  display: flex;
  gap: 1em;
}
.cardAvatar{ 
  width: 120px;
  height: 120px;
  object-fit: cover;
  border-radius: 1em;
}
.cardUserInfo { 
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.cardName{ 
  font-size: 1em;
  font-weight: 600;
  color: white;
}
.cardCity{ 
  font-size: 0.95em;
  font-weight: 600;
  color: rgb(137, 137, 137);
}
.cardInfo{ 
  display: flex;
  flex-direction: column;
  gap: 0.6em;
}
.cardInfoText{ 
  font-size: 0.9em;
  font-weight: 500;
  color: white;
}
.cardInfoText strong{ 
  font-weight: 400;
  color: rgba(32, 255, 236, 0.931);
}
.cardDesc{ 
  font-size: 0.9em;
  font-weight: 400;
  color: white;
  margin: 1.5em 0;
  line-height: 1.2em;
}
.cardBtn{ 
  border: 0;
  font-size: 0.85em;
  font-weight: 550;
  background-color: #272727;
  width: 100%;
  box-sizing: border-box;
  color: white;
  padding: 0.8em 0;
  border-radius: 0.5em;
  transition: background-color .2s ease-in-out, transform .2s ease-in-out;
}
.cardBtn:hover{ 
  transform: translateY(-2px);
  background-color: #2b2b2b;
}

/* Modal */
.modalOverlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.709);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}

.modal {
  position: relative;
  background-color: #121212;
  padding: 3em;
  border-radius: 1.5em;
  width: 300px;
  text-align: center;
  color: white;
}

.closeBtn {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 30px;
  border: none;
  background: none;
  cursor: pointer;
  color: white;
}
.title { 
  font-size: 1.3em;
  font-weight: 700;
  margin-bottom: 1.5em;
}
.contact { 
  background-color: #171717;
  border-radius: 1em;
  padding: 1em;
  margin-bottom: 1.5em;
}
.phoneNumber {
  font-size: 1.7em;
  font-weight: 650;
  margin-bottom: 0.3em;
}
.name{ 
  font-size: 0.85em;
  font-weight: 450;
}
.desc { 
  font-size: 0.95em;
  font-weight: 500;
  line-height: 1.3em;
  color: rgb(138, 156, 172);
}
.subjects { 
  background-color: #1b1b1bd2;
  padding: 1em;
  border-radius: 1em;
  display: flex;
  flex-direction: column;
  gap: 0.6em;
  margin: 1em 0;
}
.subjectItem { 
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.subjectTitle{ 
  font-size: 0.9em;
  font-weight: 500;
  color: rgb(244, 255, 255);
  cursor: pointer;
}
.subjectTitle:hover{ 
  color: aqua;
}
.subjectItemDesc {
  display: flex;
  gap: 0.5em;
}
.subjectItemDesc p {
  font-size: 0.85em;
  font-weight: 400;
  color: rgb(235, 235, 235);
}
.subjectsBtn { 
  color: white;
  font-size: 0.8em;
  font-weight: 500;
  text-align: center;
  padding: 0.7em;
  background-color: #272727;
  border-radius: 0.5em;
  cursor: pointer;
  transition: background-color .2s ease-out ;
}
.subjectsBtn:hover { 
  background-color: #303030;
}
.readMoreBtn {
  background: none;
  border: none;
  color: rgba(32, 255, 236, 0.931);
  cursor: pointer;
  margin-left: 0.5em;
  font-size: 0.9em;
  font-weight: 500;
  padding: 0;
}
.readMoreBtn:hover {
  text-decoration: underline;
}


/* Responsive Styles */
@media (max-width: 1200px) { /* Laptop */
  .card {
      width: 45%;
      padding: 1.2em;
  }
  .cardUser {
      gap: 0.8em;
  }
  .cardAvatar {
      width: 100px;
      height: 100px;
  }
  .cardName {
      font-size: 0.95em;
  }
  .cardCity {
      font-size: 0.85em;
  }
  .cardDesc {
      font-size: 0.85em;
  }
}

@media (max-width: 768px) { /* Tablet */
  .card {
      width: 70%;
      padding: 1em;
  }
  .cardUser {
      gap: 0.5em;
  }
  .cardAvatar {
      width: 80px;
      height: 80px;
  }
  .cardName {
      font-size: 0.9em;
  }
  .cardCity {
      font-size: 0.8em;
  }
  .cardDesc {
      font-size: 0.8em;
  }
  .cardBtn {
      padding: 0.7em 0;
  }
}

@media (max-width: 480px) { /* Mobile */
  .card {
      width: 100%;
      padding: 1.5em;
      border-radius: 1em;
  }
  .cardUser {
      align-items: center;
      gap: 1em;
  }
  .cardAvatar {
      width: 6em;
      height: 6em;
  }
  .cardName {
      font-size: 0.85em;
      margin-bottom: 0.5em;
  }
  .cardCity {
      font-size: 0.75em;
      margin-bottom: 0.5em;
  }
  .cardInfo{ 
    display: flex;
    flex-direction: column;
    gap: 0.3em;
  }
  .subjectTitle { 
    font-size: 0.7em;
    font-weight: 500;
  }
  .subjectItemDesc{ 
    font-size: 0.75em;
    font-weight: 600;
  }
  .cardInfoText{ 
    font-size: 0.75em;
    font-weight: 400;
  }
  .cardInfoText strong{ 
    font-weight: 500;
    color: rgba(32, 255, 236, 0.931);
  }
  .cardDesc {
      font-size: 0.75em;
  }
  .cardBtn {
      padding: 0.9em 0;
      font-size: 0.7em;
  }
  .subjectsBtn { 
    padding: 0.9em 0;
    font-size: 0.7em;
  }
}
