.subjectsContainer {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 1em;
    background-color: #121212;
    padding: 0.5em;
    border-radius: 0 0 1em 1em;
    width: 90%; /* Уменьшенная ширина */
    margin-left: auto;
    margin-right: auto;
  }
  
  .subjectButton {
    padding: 0.8em 1.5em;
    font-size: 0.9em;
    font-weight: 600;
    color: white;
    background-color: #202020;
    border: none;
    border-radius: 0.8em;
    cursor: pointer;
    transition: background-color 0.2s, transform 0.2s;
  }
  
  .subjectButton:hover {
    background-color: #2b2b2b;
    transform: translateY(-3px);
  }
  