/* General Styles */
.header { 
  box-sizing: border-box;
  position: relative;
  background-color: #121212;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1em 3em;
  border-radius: 1.5em;
  user-select: none;
  margin-top: 0.5em;
}
.title { 
  font-size: 1em;
  font-weight: 700;
  color: #ffffff;
}
.headerTiteles { 
  display: flex;
  align-items: center;
  gap: 3em;
}
.headerTitle{ 
  color: white;
  font-size: 0.95em;
  font-weight: 550;
  background-color: #202020;
  padding: 1em 3em;
  border-radius: 1em;
  cursor: pointer;
  transition:  background-color .2s ease-in-out, transform .2s ease-in-out;
}
.headerTitle:hover{ 
  transform: translateY(-3px);
  background-color: #2b2b2b;
}
.headerSmallTitle { 
  font-size: 0.85em;
  font-weight: 550;
  color: white;
}
.btn { 
  cursor: pointer;
  font-size: 0.85em;
  font-weight: 550;
  color: white;
  padding: 0.9em 2em;
  border-radius: 1em;
  background: none;
  border: 1px solid white;
  transition: background-color .2s ease-in-out, color .2s ease-in-out;
}
.btn:hover { 
  background-color: white;
  color: #121212;
}

.modalOverlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal {
  background: #ffffff;
  padding: 2em;
  border-radius: 1em;
  width: 400px;
  max-width: 90%;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
  position: relative;
}

.closeBtn {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  font-size: 1.5em;
  cursor: pointer;
  color: #121212;
}

.error {
  color: red;
  font-size: 0.9em;
}

form {
  display: flex;
  flex-direction: column;
  gap: 1em;
}

input {
  padding: 0.8em;
  font-size: 1em;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.submitBtn {
  background: #121212;
  color: white;
  padding: 0.8em;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.submitBtn:hover {
  background: #333;
}

.toggleText {
  font-size: 0.9em;
  color: blue;
  text-decoration: underline;
  cursor: pointer;
  margin-top: 1em;
}

/* Responsive Styles */
@media (max-width: 1200px) { /* Laptop */
  .header {
      padding: 1em 2em;
  }
  .headerTiteles {
      gap: 2em;
  }
  .headerTitle {
      font-size: 0.9em;
      padding: 1em 2.5em;
  }
  .headerSmallTitle {
      font-size: 0.8em;
  }
  .btn {
      padding: 0.8em 2em;
  }
}

@media (max-width: 768px) { /* Tablet */
  .header {
      padding: 1em 1.5em;
  }
  .headerTiteles {
      gap: 1.5em;
  }
  .headerTitle {
      font-size: 0.75em;
      font-weight: 600;
      padding: 1em 2.5em;
  }
  .headerSmallTitle {
      font-size: 0.75em;
  }
  .btn {
      padding: 0.8em 1.8em;
      font-size: 0.75em;
  }
  .modal {
      width: 90%;
      padding: 1.5em;
  }
}

@media (max-width: 550px) { /* Mobile */
  .header {
      padding: 1em 2em;
      border-radius: 1em;
  }
  .title { 
    font-size: 0.85em;
    font-weight: 700;
  }
  .headerSmallTitle { 
    display: none;
  }
  .headerTiteles {
      gap: 0.8em;
  }
  .headerTitle {
      font-size: 0.7em;
      padding: 0.9em 2em;
  }
  .headerSmallTitle {
      font-size: 0.6em;
  }
  .btn { 
      font-size: 0.65em;
      padding: 0.9em 1.7em;
  }
  .modal {
      width: 90%;
      padding: 1em;
  }
  .submitBtn {
      padding: 0.7em;
  }
}
