  /* Модальное окно */
  /* Фиксация заднего фона */
.modalOverlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  overflow: hidden; /* Запрещаем прокрутку фона */
}

/* Модальное окно */
.modal {
  background-color: #121212;
  padding: 2em;
  border-radius: 1em;
  max-width: 500px;
  width: 90%;
  max-height: 90%; /* Устанавливаем максимальную высоту */
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.25);
  position: relative;
  animation: fadeIn 0.3s ease;
  overflow-y: auto; /* Включаем вертикальную прокрутку */
}

/* Скроллбар для модального окна */
.modal::-webkit-scrollbar {
  width: 8px;
}

.modal::-webkit-scrollbar-thumb {
  background-color: #444;
  border-radius: 4px;
}

.modal::-webkit-scrollbar-thumb:hover {
  background-color: #666;
}

  
  /* Анимация появления модального окна */
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: scale(0.95);
    }
    to {
      opacity: 1;
      transform: scale(1);
    }
  }
  
  /* Кнопка закрытия модального окна */
  .closeBtn {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    color: #ffffff;
    font-size: 1.8rem;
    cursor: pointer;
    transition: color 0.2s ease;
  }
  
  .closeBtn:hover {
    color: #ff4d4d;
  }
  
  /* Форма добавления */
  .addAdsForm {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
  }
  
  .subjectItem {
    background-color: #1d1d1d;
    padding: 1.2rem;
    border-radius: 1em;
    display: flex;
    flex-wrap: wrap;
    gap: 0.6em;
    align-items: center;
  }
  
  /* Инпуты и текстовые области */
  .subjectItem input,
  textarea {
    box-sizing: border-box;
    background-color: #1e1e1e;
    border: 1px solid #313131;
    padding: 0.6em 1em;
    border-radius: 0.8em;
    color: #ffffff;
    width: 100%;
    font-size: 0.9em;
    outline: none;
    transition: border-color 0.3s ease, box-shadow 0.3s ease;
  }
  
  .subjectItem input:focus,
  textarea:focus {
    border-color: #4ca7af;
  }
  
  /* Чекбоксы и их лейблы */
  .subjectItem label {
    display: flex;
    align-items: center;
    font-size: 0.85em;
    color: #f2f2f2;
    cursor: pointer;
    transition: color 0.2s ease;
  }
  
  .subjectItem label:hover {
    color: #4cacaf;
  }
  
  .subjectItem label input[type="checkbox"] {
    margin-right: 1em;
    width: 2em;
    height: 2em;
    border: 1px solid #444;
    border-radius: 0.5em;
    background: #1e1e1e;
    appearance: none;
    cursor: pointer;
    transition: background-color 0.3s ease, border-color 0.3s ease;
  }
  
  .subjectItem label input[type="checkbox"]:checked {
    background-color: #4cacaf;
    border-color: #4cacaf;
  }
  
  .subjectItem label input[type="checkbox"]:hover {
    border-color: #4cacaf;
  }
  
  /* Кнопки для управления элементами */


  .delButton { 
    border: 1px solid rgb(255, 80, 80);
    background: none;
    color: rgb(255, 80, 80);
    padding: 0.5em 1.2em;
    border-radius: 0.8em;
    font-size: 0.9em;
    font-weight: 500;
    transition: color .25s ease-in-out, background-color .25s ease-in-out;
  }
  .delButton:hover { 
    color: white;
    background-color: rgb(255, 80, 80);
  }
  .addObjectBtn { 
    border: 1px solid rgb(80, 255, 83);
    background: none;
    color: rgb(80, 255, 83);
    padding: 0.5em 1.2em;
    border-radius: 0.8em;
    font-size: 0.9em;
    font-weight: 500;
    transition: color .25s ease-in-out, background-color .25s ease-in-out;
  }
  .addObjectBtn:hover { 
    color: white;
    background-color: rgb(80, 255, 83);
  }
  
  @media (max-width: 1200px) {
