.panel {
    position: relative;
    background-color: #121212;
    display: flex;
    align-items: center;
    gap: 1em;
    padding: 2em;
    border-radius: 1.5em;
    user-select: none;
    margin-top: 0.5em;
}
.modalOverlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 999;
  }
  
  .modal {
    background: white;
    padding: 20px;
    border-radius: 8px;
    width: 400px;
    max-width: 100%;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  @media (max-width: 1200px) {
    .panel {
      padding: 2em;
  }
  }
  @media (max-width: 768px) {
    .panel {
      padding: 1.5em;
      border-radius: 1em;
  }
  }
  @media (max-width: 550px) { 
    .panel {
      padding: 1em;
      border-radius: 1em;
  }
  }
